<style>
  .class .container{
    display: flex;
  }
  .class .btn{
    width: 25%;
    padding-bottom: 60px;
    height: 80vh;
    overflow: scroll;
  }
  .class .btn>li{
    box-sizing: border-box;
    padding: 14px;
  }
  .class .cont{
    width: 75%;
    background: rgb(204, 204, 204);
  }
  .class .btn .active{
    color: #26a2ff;
    border-bottom: 3px solid #26a2ff;
    margin-bottom: -3px;
  }
  .class .imgs{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    /* padding: 10px; */
  }
  .class .imgs>li{
    width: 30%;
    margin: 1%;
  }
  .class .imgs>li>img{
    width: 100%;
  }
</style>

<template>
  <div class="class">
    <div class="container">
      <ul class="btn" @click="change">
        <li :class="select == i?'active':''" v-for="(item,i) of num" :key="i" :data-index="i">分类{{item}}</li>
      </ul>
      <div class="cont">
        <p>分类{{title}}</p>
        <ul class="imgs">
          <li v-for="(item,i) of img" :key="i">
            <img src="../assets/images/1.jpg" alt="">
            <p>品牌{{item}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      num: 14,
      select: 0,
      img: 8
    }
  },
  methods:{
    change(e){
      if(e.target.nodeName == "LI"){
        this.select = e.target.dataset.index
      }
    }
  },
  computed:{
    title(){
      var a = this.select
      return ++a
    }
  }
}
</script>